import { Component, OnInit } from "@angular/core";
import {
  ILastViewedCourseModel,
  IMyCoursesMV,
  IMyDiplomasMV,
  IRecAndBasicCoursesMV,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { AppAssets } from "app/shared/data/app-assets";
import { GeneralService } from "app/shared/services/General/general.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.scss"],
})
export class HomeComponent implements OnInit {
  continueCourses: IMyCoursesMV[] = [];
  basicCourses: IMyCoursesMV[] = [];
  recommendedCourses: IMyCoursesMV[] = [];
  diplomas: IMyDiplomasMV[] = [];
  isLoadedCourses: boolean = false;
  isLoadedDiplomas: boolean = false;

  userName: string = "";
  userId: number = 0;
  lastUserCourse: ILastViewedCourseModel;

  constructor(
    public authservice: AuthService,
    public appAssets: AppAssets,
    public languageService: LanguageService,
    private courseService: CourseService,
    private generalService: GeneralService,
    public fileService: UploadFileService
  ) {
    //get user data
    this.generalService.userData.subscribe((user) => {
      this.userName = user?.user.first_name.trim().split(" ")[0];
      if (this.userId != user?.user.user_ID) {
        this.userId = user?.user.user_ID;
        if (this.userId > 0) this.getAllDiplomas();
      }
    });
  }

  ngOnInit() {
    if (this.authservice.isUserLoggedIn()) {
      this.getAllEmployeeCourses();
      if (this.authservice.isStudent()) this.getLastUserCourse();
    }
  }

  getAllEmployeeCourses() {
    this.courseService
      .getBasicAndRecommendedCourses()
      .subscribe((res: IRecAndBasicCoursesMV) => {
        this.continueCourses = res.courses.filter((course) => course.isCurrent);
        this.basicCourses = res.courses.filter(
          (course) => course.basic && !course.isCurrent
        );
        this.recommendedCourses = res.courses.filter(
          (course) => course.recommedned && !course.isCurrent
        );
        this.isLoadedCourses = true;
        // console.log(
        //   this.continueCourses,
        //   this.basicCourses,
        //   this.recommendedCourses
        // );
      });
  }

  getLastUserCourse() {
    this.courseService.getLastCourseUser().subscribe((res: any) => {
      if (res.returnValue != 0) this.lastUserCourse = res;
    });
  }

  getAllDiplomas() {
    this.courseService
      .getAllDiploma(this.userId)
      .subscribe((res: any[]) => {
        this.diplomas = res;
        this.isLoadedDiplomas = true;
      });
  }

  scrollToSection(className: string) {
    const element: any = document.getElementsByClassName(className)[0];
    if (element) {
      let top = element.offsetTop;
      window.scroll({ top: top, behavior: "smooth" });
    }
  }
}
