import { Component, Input, OnInit } from "@angular/core";
import {
  CategorieResponse,
  CategoriesModel,
} from "app/admin/models/setup/categories";
import { CategoriesService } from "app/admin/services/setup/categories.service";
import { AppAssets } from "app/shared/data/app-assets";
import { LanguageService } from "app/shared/services/language.service";
import { Storage } from "app/shared/services/local-storage";

@Component({
  selector: "app-home-categories",
  templateUrl: "./home-categories.component.html",
  styleUrls: ["./home-categories.component.scss"],
})
export class HomeCategoriesComponent implements OnInit {
  @Input() isnavBar: boolean = false;
  @Input() showTitle: boolean = false;
  @Input() title: string = "exploreCategories";
  @Input() showAllItems: boolean = false;
  categories: CategoriesModel[] = [];
  totalItems: number;
  eduCompId: number = null;
  isLoaded: boolean = false;

  constructor(
    private categoriesService: CategoriesService,
    public languageService: LanguageService,
    public appAssets: AppAssets
  ) {}

  ngOnInit() {
    this.eduCompId = Storage.getEduCompId() ?? null;
    if (this.eduCompId == 0) this.eduCompId = null;
    this.getAllCategories();
  }

  getAllCategories() {
    this.categoriesService
      .getCategories(/* this.eduCompId */)
      .subscribe((res: CategorieResponse) => {
        if (res.returnValue == 1) this.totalItems = res.MainCategories.length;
        this.categories = this.showAllItems
          ? res.MainCategories
          : res.MainCategories.slice(0, 10);
        this.isLoaded = true;
      });
  }
}
